import {useEffect, useState} from 'react';
const width = 400;
const height = 100;
const radius = 5;
const r = {r: radius};

const closeEnough = (a, b) => {
  return b < a + 0.1 && b > a - 0.1;
}

const scaleX = (min, max) => {
  const range = max - min;
  const w = width - 2 * radius;
  return value => {
    return (2.5 * radius) + (w - 5 * radius) * (value - min) / range;
  }
}

export default function Slider ({range: [min, max], step, onChange}) {
  const [value, setValue] = useState (min);
  const [mouseIsDown, setMouseIsDown] = useState (false);
  const roundToNearestStep = () => {
    let newVal = Math.round (min + (value - min) / step) * step;
    if (newVal < min) newVal = min; 
    if (newVal > max) newVal = max;
    return newVal;
  }
  const pos = scaleX (min, max);
  const drag = e => {
    if (!mouseIsDown) return;
    let newVal = value + (e.movementX * (max - min) / width);
    if (newVal < min) newVal = min; 
    if (newVal > max) newVal = max;
    setValue (newVal);
  }
  useEffect (() => {
    onChange && onChange (roundToNearestStep ())
  }, [value])
  return (
    <svg onTouchMove={drag} onTouchCancel={() => setMouseIsDown (false)} onMouseMove={drag} onMouseLeave={() => setMouseIsDown (false)} onMouseUp={() => setMouseIsDown (false)} viewBox={`0 0 ${width} ${height}`} className="component-slider-svg">
      <g className="slider-base">
        <circle cx={pos (min)} cy={height / 2} {...r} />
        <line x1={pos (min) + radius} x2={pos (max) - radius} y1={height / 2} y2={height / 2} />
        <circle cx={pos (max)} cy={height / 2} {...r} />
        <text x={pos (min)} y={height * .8} textAnchor="left">{min}</text>
        <text x={pos(max)} y={height * .8} textAnchor="end">{max}</text>
      </g>
      <g className="slider-value">
        <circle onDragStart={() => setMouseIsDown (true)} onMouseDown={() => setMouseIsDown (true)} cx={pos (value)} cy={height / 2} r={radius * 2} />
        <line x1={pos (value)} x2={pos (value)} y1={height * .25} y2={height * .35}></line>
        <text x={pos (value)} y={height * .2} textAnchor="middle">{roundToNearestStep (value)}</text>
      </g>
    </svg>
  )
}